import Vue from 'vue'
import Router from 'vue-router'
import SenbaoIndex from '@/page/index'
import companyDesc from "@/page/aboutUs/companyDesc"
import companyCulture from "@/page/aboutUs/companyCulture"
import organization from "@/page/aboutUs/organization"
import securityControl from "@/page/productApplication/securityControl"
import carElectronic from "@/page/productApplication/carElectronic"
import digitalProduct from "@/page/productApplication/digitalProduct"
import medicalEquipment from "@/page/productApplication/medicalEquipment"
import factoryControlEquipment from "@/page/productApplication/factoryControlEquipment"
import afterService from "@/page/serviceSupport/afterService"
import contactUs from "@/page/contactUs/contactUs"
import firstProduct from "@/page/productSupport/firstProduct"
import secondProduct from "@/page/productSupport/secondProduct"
import thirdProduct from "@/page/productSupport/thirdProduct"
import forthProduct from "@/page/productSupport/forthProduct"
import fifthProduct from "@/page/productSupport/fifthProduct"
import sixthProduct from "@/page/productSupport/sixthProduct"
import seventhProduct from "@/page/productSupport/seventhProduct"
import eighthProduct from "@/page/productSupport/eighthProduct"
import ninethProduct from "@/page/productSupport/ninethProduct"

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: "/", redirect: "/index.html" },
    {
      path: '/index.html',
      name: 'SenbaoIndex',
      component: SenbaoIndex
    }, {
      path: '/aboutus.html',
      name: 'aboutus',
      redirect: '/introduction.html'
    },
    {
      path: '/introduction.html',
      name: 'introduction',
      component: companyDesc
    }, {
      path: '/organization.html',
      name: 'organization',
      component: organization
    }, {
      path: '/companyCulture.html',
      name: 'companyCulture',
      component: companyCulture
    }, {
      path: "/product/product_001.html",
      name: "firstProduct",
      component: firstProduct
    }, {
      path: "/product/product_002.html",
      name: "secondProduct",
      component: secondProduct
    }, {
      path: "/product/product_003.html",
      name: "thirdProduct",
      component: thirdProduct
    }, {
      path: "/product/product_004.html",
      name: "forthProduct",
      component: forthProduct
    }, {
      path: "/product/product_005.html",
      name: "fifthProduct",
      component: fifthProduct
    }, {
      path: "/product/product_006.html",
      name: "sixthProduct",
      component: sixthProduct
    }, {
      path: "/product/product_007.html",
      name: "seventhProduct",
      component: seventhProduct
    }, {
      path: "/product/product_008.html",
      name: "eighthProduct",
      component: eighthProduct
    }, {
      path: "/product/product_009.html",
      name: "ninethProduct",
      component: ninethProduct
    }, {
      path: "/securityControl.html",
      name: "securityControl",
      component: securityControl
    }, {
      path: "/carElectronic.html",
      name: "carElectronic",
      component: carElectronic
    }, {
      path: "/digitalProduct.html",
      name: "digitalProduct",
      component: digitalProduct
    }, {
      path: "/medicalEquipment.html",
      name: "medicalEquipment",
      component: medicalEquipment
    }, {
      path: "/factoryControlEquipment.html",
      name: "factoryControlEquipment",
      component: factoryControlEquipment
    },
    {
      path: "/afterService.html",
      name: "afterService",
      component: afterService
    }, {
      path: "/contactUs.html",
      name: "contactUs",
      component: contactUs
    },
    {
      path: "*",
      redirect: "/index.html"
    }

  ]
})



